"use client";
import { useSearchParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";

import styles from "./VerificaCorreo.module.css";

import { useAlerta } from "@/context/AlertaContext";
import { resendVerificationEmail } from "@/services/authService";

export default function VerificaCorreo() {
  const [email, setEmail] = useState("");
  const [expiresAt, setExpiresAt] = useState<Date | null>(null);
  const [tiempoRestante, setTiempoRestante] = useState<number | null>(null);
  const [cargando, setCargando] = useState(false);
  const { mostrarAlerta } = useAlerta();
  const router = useRouter();
  const params = useSearchParams();

  useEffect(() => {
    if (!params) return;

    const emailParam = params.get("email");
    const expiresParam = params.get("expiresAt");

    if (emailParam) setEmail(emailParam);
    if (expiresParam) setExpiresAt(new Date(expiresParam));
  }, [params]);

  useEffect(() => {
    if (!expiresAt) return;

    const updateTiempo = () => {
      const restante = expiresAt.getTime() - Date.now();
      setTiempoRestante(restante > 0 ? restante : 0);
      if (restante <= 0) {
        mostrarAlerta(
          "Tu enlace de verificación ha expirado. Regístrate de nuevo.",
          "error"
        );
        router.push("/auth/register");
      }
    };

    updateTiempo();
    const interval = setInterval(updateTiempo, 1000);
    return () => clearInterval(interval);
  }, [expiresAt, mostrarAlerta, router]);

  const handleReenviar = async () => {
    setCargando(true);
    try {
      const res = await resendVerificationEmail(email);
      mostrarAlerta(res.message, "success");
    } catch (err: unknown) {
      const mensaje =
        err instanceof Error
          ? err.message
          : typeof err === "object" && err !== null && "message" in err
            ? String((err as { message?: unknown }).message)
            : "Error al reenviar el correo.";
      mostrarAlerta(mensaje, "error");
    } finally {
      setCargando(false);
    }
  };

  const minutos = tiempoRestante ? Math.floor(tiempoRestante / 60000) : 0;
  const segundos = tiempoRestante
    ? Math.floor((tiempoRestante % 60000) / 1000)
    : 0;

  return (
    <section className={styles.container}>
      <h2>Verifica tu correo</h2>
      <p>Te enviamos un correo con un enlace para verificar tu cuenta.</p>

      <input
        type="email"
        placeholder="Tu correo"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        className={styles.input}
      />

      {tiempoRestante !== null && tiempoRestante > 0 && (
        <>
          <p className={styles.mensaje}>
            Tu enlace expira en{" "}
            <strong>
              {minutos}:{segundos.toString().padStart(2, "0")}
            </strong>
          </p>

          {tiempoRestante < 5 * 60 * 1000 && (
            <p className={styles.alertaUrgente}>
              ⚠️ ¡Quedan menos de 5 minutos para verificar tu cuenta!
            </p>
          )}

          <button
            onClick={handleReenviar}
            disabled={cargando || !email}
            className={styles.boton}
          >
            {cargando ? "Reenviando..." : "Reenviar correo"}
          </button>
        </>
      )}

      {tiempoRestante === 0 && (
        <p className={styles.mensaje}>
          Tu enlace ha expirado.{" "}
          <a href="/auth/register">Regístrate de nuevo</a>.
        </p>
      )}
    </section>
  );
}
